/*============================
         Main Login
  ============================*/

.g-main-login-area {
  position: relative;
  display: flex;
  justify-content: space-between;
  background-color: #CFFDE1;
  background-image: url("../images/login/login-bg.jpg");
  background-position: right center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
  overflow: hidden;

  &:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
  }
}

.g-login-p-logo {
    width: 120px;
    margin: auto;
}

.g-main-login-form {
    z-index: 3;
    position: relative;
    max-width: 500px;
    width: 100%;
    height: 100vh;
    background: #00B4DB; /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #0083B0, #00B4DB); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    box-shadow: 15px 0 15px -10px rgba(0, 0, 0, 0.25);
}

.g-main-login-form-main {
    width: 100%;
    padding: 0 3rem;
    color: #ffffff;
}

.g-powered-by {
    max-width: 450px;
    display: flex;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
}

.g-main-login-right {
    position: relative;
    height: 100vh;
    width: calc(100vw - 450px);
}

.g-login-brand-logo {
    position: absolute;
    bottom: 30px;
    right: 30px;
    z-index: 3;

    a {
        width: 120px;
        display: block;
    }
}

.g-signin-form {
    input.form-control {
        background-color: darken(#6b6b6b, 10%);
        color: #ffffff;
    }

    /* Change the white to any color */
    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    input:-webkit-autofill:active {
        -webkit-box-shadow: 0 0 0 1000px transparent inset;

    }
}

.g-form-meta {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.g-f-password {
    text-decoration: underline;
    cursor: pointer;
}

.g-input-error {
    position: relative;
    float: right;
    color: #ff898b;
}

.g-password-redirect {
    display: none;
}

.redirect {
    cursor: pointer;
    text-align: center;
    text-decoration: underline;
}
